.reddit_widget__app {
  -webkit-font-smoothing: antialiased;
  color: #222;
  border-radius: 12px;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px;
  overflow: hidden;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, .07);
}

.reddit_widget__app a {
  color: #5399d7;
  transition: all .2s ease-in-out;
}

.reddit_widget__header {
  color: #fff;
  text-align: center;
  background: #5399d7;
  margin: 0;
  padding: 12px 6px;
  font-size: 19px;
  display: block;
}

.reddit_widget__header a {
  color: #fff;
}

.reddit_widget__inner {
  padding: 12px;
}

.reddit_widget__inner a {
  color: #222;
  text-decoration: none;
}

.reddit_widget__post {
  flex-direction: column;
  padding: 6px 0;
  display: flex;
}

.reddit_widget__posted_by {
  color: #787c7e;
  padding: 0 0 6px;
  font-size: 14px;
}

.reddit_widget__posted_by a {
  color: #787c7e;
}

a.reddit_widget__posted_by:hover {
  text-decoration: underline;
}

a.reddit_widget__title {
  font-weight: 500;
}

a.reddit_widget__title:hover {
  color: #000;
}

.reddit_widget__powered_by {
  text-align: center;
  font-size: 14px;
}

